import React, { Component } from 'react';
import { withRouter } from 'react-router';
import PropTypes from 'prop-types';
import Helmet from 'react-helmet';
import logo from 'assets/images/common/PolicyPalLogo.png';

import { isProduction } from 'lib/tagFunctions';
import { FB_ID } from '../../constants';

const SITE_URL = isProduction ? 'https://www.policypal.com' : 'https://web.policypal.com';

const FACEBOOK_APP_ID = FB_ID;

const defaultTitle = 'PolicyPal 2.0';
const defaultDescription = 'PolicyPal is the insurance mobile app that helps you manage and get quality insurance policies at affordable prices.';
const defaultImage = `${SITE_URL}${logo}`;
const defaultTwitter = '@hipolicypal';
const defaultSep = ' - ';

class Page extends Component {
  getMetaTags(
    {
      title,
      description,
      image,
      contentType,
      twitter,
      noCrawl,
      published,
      updated,
      category,
      tags,
    },
    pathname,
  ) {
    const theTitle = title
      ? (defaultTitle + defaultSep + title).substring(0, 60)
      : defaultTitle;
    const theDescription = description
      ? description.substring(0, 155)
      : defaultDescription;
    const theImage = image ? `${SITE_URL}${image}` : defaultImage;

    const metaTags = [
      { itemprop: 'name', content: theTitle },
      { itemprop: 'description', content: theDescription },
      { itemprop: 'image', content: theImage },
      { name: 'description', content: theDescription },
      { name: 'twitter:card', content: 'summary_large_image' },
      { name: 'twitter:site', content: defaultTwitter },
      { name: 'twitter:title', content: theTitle },
      { name: 'twitter:description', content: theDescription },
      { name: 'twitter:creator', content: twitter || defaultTwitter },
      { name: 'twitter:image:src', content: theImage },
      { property: 'og:title', content: theTitle },
      { property: 'og:type', content: contentType || 'website' },
      { property: 'og:url', content: SITE_URL + pathname },
      { property: 'og:image', content: theImage },
      { property: 'og:description', content: theDescription },
      { property: 'og:site_name', content: defaultTitle },
      { property: 'fb:app_id', content: FACEBOOK_APP_ID },
    ];

    if (noCrawl) {
      metaTags.push({ name: 'robots', content: 'noindex, nofollow' });
    }

    if (published) {
      metaTags.push({ name: 'article:published_time', content: published });
    }
    if (updated) {
      metaTags.push({ name: 'article:modified_time', content: updated });
    }
    if (category) {
      metaTags.push({ name: 'article:section', content: category });
    }
    if (tags) {
      metaTags.push({ name: 'article:tag', content: tags });
    }

    return metaTags;
  }

  render() {
    const {
      children,
      id,
      className,
      location,
      ...rest
    } = this.props;

    const { pathname, search } = location;
    const path = `${pathname}${search}`;

    return (
      <div id={id} className={className}>
        <Helmet
          htmlAttributes={{
            lang: 'en',
            itemscope: undefined,
            itemtype: `http://schema.org/${rest.schema || 'WebPage'}`,
          }}
          title={
            rest.title ? defaultTitle + defaultSep + rest.title : defaultTitle
          }
          link={[{
              rel: 'canonical',
              href: SITE_URL + path,
          }]}
          meta={this.getMetaTags(rest, path)}
        />
        {children}
      </div>
    );
  }
}

Page.defaultProps = {
  className: '',
};

Page.propTypes = {
  id: PropTypes.string.isRequired,
  className: PropTypes.string,
  location: PropTypes.object.isRequired,
};

export default withRouter(Page);
